			getData() {
				let _this = this;
				//获取店铺信息
				getShopinfo({
					get_shopinfo_base: 1
				}, function(resp) {
					if(resp) {
						_this.shopinfo = resp.data.shopinfo || {};
						_this.imageUrl = _this.shopinfo.shop_logo;
						_this.busType = businessType.code;
						if(_this.phone === '') {
							_this.phone = _this.shopinfo.telephone;
						}
					}
				});
				//获取店铺设置
				getShopinfo({
					get_shopinfo_edit: 1
				}, function(resp) {
					if(resp) {
						_this.shopsystem = resp.data.shopinfo || {};
						_this.system = Startsystem.code;
						_this.pay = Payway.code;
						_this.paytime = PayTime.code;
						_this.sale = Saleway.code;
						if(!Util.isEmpty(_this.shopsystem.is_invoice_vat)) {
							_this.Invos = _this.shopsystem.is_invoice_vat.is_invoice;
						}
						_this.invos = Invoce.code;
						if(!Util.isEmpty(_this.shopsystem.opening_time)) {
							if(_this.shopsystem.opening_time.length === 0) {
								_this.showTime = true;
							}
						}
						let Arr = [],
							arr = [];
						if(_this.shopsystem.opening_time) {
							_this.shopsystem.opening_time.forEach((item) => {
								if(item.type == Dinnertime.MORN) {
									let mons = `${Dinnertime.toString(Dinnertime.MORN)}:${item.from.hh}:${item.from.mm}:${item.from.ss}-${item.to.hh}:${item.to.mm}:${item.to.ss} `;
									Arr.push(mons);
								} else if(item.type == Dinnertime.NOON) {
									let non = `${Dinnertime.toString(Dinnertime.NOON)}:${item.from.hh}:${item.from.mm}:${item.from.ss}-${item.to.hh}:${item.to.mm}:${item.to.ss}  `;
									Arr.push(non);
								} else if(item.type == Dinnertime.EVEN) {
									let even = `${Dinnertime.toString(Dinnertime.EVEN)}:${item.from.hh}:${item.from.mm}:${item.from.ss}-${item.to.hh}:${item.to.mm}:${item.to.ss}  `;
									arr.push(even);
								} else if(item.type == Dinnertime.NIGHT) {
									let nit = `${Dinnertime.toString(Dinnertime.NIGHT)}:${item.from.hh}:${item.from.mm}:${item.from.ss}-次日${item.to.hh}:${item.to.mm}:${item.to.ss}  `;
									arr.push(nit);
								}
							});
							_this.Times = Arr;
							_this.Timed = arr;

						}
					}
				});
				//获取工商信息
				getShopinfo({
					get_shop_business: 1,
				}, function(resp) {
					if(resp) {
						_this.shopsbusiness = resp.data.shopsbusiness || {};
